import React from "react";
import { Chart, Geom, Axis, Tooltip, Coord, Label } from "bizcharts";
import DataSet from "@antv/data-set";
import { formatAmount } from "./../utils/format";
import moment from "moment";

class Donut extends React.Component {
  state = {
    startDate: "2017-08-15",
    // 当前期数
    currentNper: 0
  };
  componentDidMount() {
    // https://www.cnblogs.com/candy-Yao/p/9945306.html
    // 获取差值
    let diff = moment().diff(moment(this.state.startDate));
    // 获取当前期数
    let index = Math.ceil(moment.duration(diff).asMonths());
    console.warn(index)
    this.setState({
      currentNper: index
    });
  }
  render() {
    const { DataView } = DataSet;
    const data = [
      {
        item: "已还",
        count: this.state.currentNper
      },
      {
        item: "剩余",
        count: 180- this.state.currentNper
      }
    ];
    console.warn(data)
    const dv = new DataView();
    dv.source(data).transform({
      type: "percent",
      field: "count",
      dimension: "item",
      as: "percent"
    });
    const cols = {
      percent: {
        formatter: val => {
          val = formatAmount(val * 100) + "%";
          return val;
        }
      }
    };
    return (
      <Chart
        height={280}
        data={dv}
        scale={cols}
        padding={[30, 20, 30, 20]}
        forceFit
      >
        <Coord type={"theta"} radius={0.75} innerRadius={0.6} />
        <Axis name="percent" />
        <Tooltip
          showTitle={false}
          itemTpl='<li><span style="background-color:{color};" class="g2-tooltip-marker"></span>{name}: {value}</li>'
        />
        <Geom
          type="intervalStack"
          position="percent"
          color="item"
          tooltip={[
            "item*percent",
            (item, percent) => {
              percent = percent * 100 + "%";
              return {
                name: item,
                value: percent
              };
            }
          ]}
          style={{
            lineWidth: 1,
            stroke: "#fff"
          }}
        >
          <Label
            content="percent"
            formatter={(val, item) => {
              return item.point.item + ": " + val;
            }}
          />
        </Geom>
      </Chart>
    );
  }
}
export default Donut;
